
footer {
	.xs-visible {
		display: none; } }
#navbar_dropdown {
	display: none; }
.black_label {
	margin: 0 auto;
	height: 24px;
	width: 112px;
	padding: 5px 12px;
	background-color: #000000;
	color: #ffffff;
	font-size: 11px;
	font-weight: 300;
	text-transform: uppercase; }
.black_title {
	margin-bottom: 0;
	margin-top: 38px;
	color: #222222;
	font-size: 38px;
	font-weight: 700;
	text-transform: uppercase;
	text-align: center; }
.white_label {
	margin: 0 auto;
	width: 112px;
	height: 24px;
	background-color: #ffffff;
	padding: 5px 12px;
	color: #000000;
	font-size: 11px;
	font-weight: 300;
	text-transform: uppercase;
	text-align: center; }
.white_title {
	margin-bottom: 0;
	margin-top: 32px;
	color: #ffffff;
	font-size: 38px;
	font-weight: 700;
	text-transform: uppercase;
	text-align: center; }
.white_desc {
	color: #ffffff;
	font-size: 17px;
	font-weight: 400;
	text-align: center; }
header {
	position: relative;
	.subheader {
		position: relative;
		top: 50px;
		width: 100%;
		z-index: 2;
		height: 84px;
		background-color: rgba(0, 0, 0, .6); } }
nav {
	position: fixed;
	width: 100%;
	height: 50px;
	background-color: #212121;
	z-index: 10;
	transition: top 0.3s; }
.menu {
	display: flex;
	justify-content: space-between;
	padding: 13px 70px;
	margin: 0;
	list-style-type: none; }
.hanburger {
	display: none; }
.scrollLock {
	overflow: hidden; }
.menu {
	&_link {
		color: #ffffff;
		font-size: 15px;
		font-weight: 300;
		text-decoration: none;
		transition: color 0.3s ease;
		&:hover {
			color: $color-mint; } }
	&_item {
		border-right: 2px dotted black;
		padding-right: 15px;
		&:last-child {
			border-right: none; } } }
.subheader {
	&_logo {
		max-width: 100%;
		display: block;
		margin-top: 16px; }
	&_official {
		margin-top: 6px;
		height: 13px;
		opacity: 0.5;
		color: #ffffff;
		font-size: 13px;
		font-weight: 400; }
	&_btn {
		margin-top: 19px;
		width: 172px;
		height: 46px;
		box-shadow: inset 0 -3px 0 rgba(0, 0, 0, 0.12);
		border: 0;
		border-radius: 2px;
		background-image: linear-gradient(to top, $color-mint 0%, #4ce2ff 100%);
		text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
		color: #ffffff;
		font-size: 14px;
		font-weight: 400;
		text-align: center;
		line-height: 46px;
		text-transform: uppercase; }
	&_tel {
		margin-top: 19px;
		color: #ffffff;
		font-size: 12px;
		font-weight: 300; }
	&_phone {
		margin-top: 8px;
		color: #ffffff;
		font-size: 18px;
		font-weight: 400;
		text-align: center;
		text-transform: uppercase;
		text-decoration: none; } }
.header_bg {
	height: 200px;
	width: 100%;
	top: -150px;
	position: absolute;
	background-color: #212121; }
.promo {
	position: relative;
	z-index: 1;
	min-height: 800px;
	margin-top: -34px;
	background: url('../img/promo_bg.jpeg') center center/cover no-repeat;
	padding: 150px 0 135px 0;
	&_header {
		margin: 0;
		color: #ffffff;
		font-size: 50px;
		font-weight: 700;
		text-align: center;
		line-height: 40px;
		text-transform: uppercase; }
	&_subheader {
		margin: 0;
		margin-top: 22px;
		color: #ffffff;
		font-size: 30px;
		line-height: 26px;
		font-weight: 400;
		text-align: center;
		text-transform: uppercase; }
	&_descr {
		margin-top: 23px;
		color: #ffffff;
		font-size: 18px;
		font-weight: 400;
		text-align: center; }
	&_btn {
		display: block;
		margin: 0 auto;
		margin-top: 118px;
		width: 259px;
		height: 63px;
		box-shadow: inset 0 -4px 0 rgba(0, 0, 0, 0.12);
		border: 0;
		border-radius: 2px;
		background-image: linear-gradient(to top, $color-mint 0%, #4ce2ff 100%);
		text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.21);
		color: #ffffff;
		font-size: 18px;
		font-weight: 400;
		line-height: 63px;
		text-align: center;
		text-transform: uppercase;
		transition: background-image 0.5s ease;
		&:hover {
			transition: background-image 0.5s ease;

			background-image: linear-gradient(to top, #0d3841 0%, #0d3841 100%); } } }

.reasons {
	padding: 71px 0 65px 0;
	&_block {
		display: flex;
		justify-content: center;
		align-items: center;
		margin-top: 35px; }
	&_round {
		display: flex;
		justify-content: center;
		align-items: center;
		margin-right: 26px;
		width: 113px;
		height: 113px;
		background-color: $color-mint;
		border-radius: 100%; }
	&_descr {
		width: 343px;
		text-align: justify; }
	&_subtitle {
		line-height: 21px;
		color: $color-mint;
		font-size: 17px;
		font-weight: 400; }
	&_text {
		color: #202020;
		font-size: 15px;
		font-weight: 300;
		line-height: 21px; } }
.caps {
	min-height: 864px;
	padding: 48px 0 80px 0;
	background: url(../img/caps_bg.jpeg) center center/400% repeat;
	&_desk {
		color: #222222;
		font-size: 17px;
		font-weight: 400;
		text-align: center;
		margin-top: 16px; }
	.row {
		margin-top: 13px; }
	&_item {
		position: relative;
		width: 320px;
		height: 280px;
		margin: 0 auto;
		cursor: pointer;
		margin-top: 19px;
		padding: 92px 28px;
		&_1 {
			background: url(../img/app/app_1.jpeg) center (center / cover) no-repeat; }
		&_2 {
			background: url(../img/app/app_2.jpeg)center (center / cover) no-repeat; }
		&_3 {
			background: url(../img/app/app_3.jpeg)center (center / cover) no-repeat; }
		&_4 {
			background: url(../img/app/app_4.jpeg)center (center / cover) no-repeat; }
		&_5 {
			background: url(../img/app/app_5.jpeg)center (center / cover) no-repeat; }
		&_6 {
			background: url(../img/app/app_6.jpeg)center (center / cover) no-repeat; }
		&_desc {

			color: #fff;
			font-size: 24px;
			font-weight: 400;
			text-align: center;
			text-transform: uppercase; }
		&_more {
			display: flex;
			justify-content: center;
			align-items: center;
			position: absolute;
			bottom: 35px;
			left: 50%;
			transform: translateX(-50%);
			width: 26px;
			height: 26px;
			background-color: #000000;
			margin: 0 auto;
			border-radius: 100%;
			cursor: pointer;
			&:before {
				content: '';
				display: block;
				width: 14px;
				height: 2px;
				background-color: $color-mint;
				transition: 0.4s all; }
			&:after {
				content: '';
				position: absolute;
				left: 50%;
				transform: translateX(-50%);
				display: block;
				height: 14px;
				width: 2px;
				background-color: $color-mint;
				transition: 0.4s all; } }
		&:hover {
			.caps_item_more {
				&::after, &::before {
					background-color: $color-red; } } } } }



.choice {
	.container {
		position: relative; }
	&_first {
		height: 500px;
		background-color: #000;
		padding-top: 57px; }
	&_second {
		min-height: 397px;
		background: url(../img/car_bg_2.jpeg) center center/400% repeat;
		padding-top: 217px;
		&_desc {
			color: #222222;
			font-size: 17px;
			font-weight: 300;
			text-align: center; } }
	&_img {
		position: absolute;
		top: 190px;
		left: 50%;
		transform: translateX(-50%); } }
.conditions {
	padding: 68px 0 52px 0;
	&_item {
		margin: 32px auto;
		width: 478px;
		&_title {
			color: $color-mint;
			font-size: 22px;
			font-weight: 400; }
		&_desc {
			list-style-type: none;
			padding-left: 50px;
			margin-top: 22px;
			li {
				position: relative;
				margin-bottom: 10px;
				color: #222222;
				font-size: 17px;
				font-weight: 300;
				text-align: left;
				&::before {
					content: '';
					display: block;
					left: -32px;
					top: 3px;
					position: absolute;
					width: 21px;
					height: 20px;
					background: url('../icons/check_blue.png') center (center/cover no-repeat); } } }
		&_end {
			color: $color-mint;
			font-weight: 300;
			font-size: 17px; } }
	.red {
		.conditions_item {
			&_title {
				color: $color-red; }
			&_desc {
				li {
					&::before {
						background: url('../icons/check_red.png') center (center/cover) no-repeat; } } } } } }
.world {
	height: 717px;
	background: url('../img/world_bg.jpeg') center (center/cover) no-repeat;
	padding-top: 49px;
	padding-bottom: 49px;
	.container {
		height: 100%; }
	&_wrapper {
		position: relative;
		height: 100%; }
	&_desc {
		margin-top: 20px;
		color: #ffffff;
		font-size: 17px;
		font-weight: 400;
		text-align: center; }
	&_flex {
		display: flex;
		height: 100%;
		justify-content: center;
		align-items: flex-end; }
	&_city {
		position: absolute;
		width: 200px;
		bottom: 0;
		left: 50%;
		transform: translateX(-50%);
		display: block;
		align-self: bottom;
		a {
			color: #1fbad6;
			font-size: 15px;
			font-weight: 400;
			text-align: center;
			text-transform: uppercase;
			text-decoration: none;
			&:hover {
				color: #fff; } } } }
footer {
	padding: 14px 23px 39px 23px;
	a {
		&:hover {
			text-decoration: none; } } }
.footer {
	&_divider {
		width: 299px;
		height: 4px;
		background-color: #1fbad6;
		margin: 0 auto; }
	&_wrapper {
		display: flex;
		justify-content: space-between;
		align-items: flex-start;
		padding-top: 32px; }
	&_wrapper > div {
		width: 100%;
		&:last-child {
			display: flex;
			justify-content: flex-end; } }
	&_social {
		width: 160px;
		display: flex;
		justify-content: space-between;
		&_item {
			display: flex;
			justify-content: center;
			align-items: center;
			margin: 0 4px;
			width: 32px;
			height: 32px;
			background-color: #070716;
			border-radius: 100%;
			text-decoration: none;
			i {
				color: #fff; } } }
	&_links {
		width: 390px;
		margin: 0 auto;
		&_main {
			display: flex;
			justify-content: space-between;
			padding: 0 10px;
			a {
				color: #070716;
				font-size: 15px;
				font-weight: 300;
				text-transform: uppercase;
				text-decoration: none; } }

		&_sub {
			display: flex;
			justify-content: space-between;
			margin-top: 10px;
			a {
				color: #999999;
				font-size: 11px;
				font-weight: 300;
				text-transform: uppercase;
				text-decoration: none; } }

		&_lang {
			display: block;
			margin: 0 auto;
			color: #1fbad6;
			font-size: 11px;
			font-weight: 400;
			margin-top: 19px;
			text-transform: uppercase;
			text-align: center;
			text-decoration: none; } }
	&_mobile {
		a {
			text-decoration: none; }
		img {
			height: 27px; } } }
.footer_line {
	width: 100%;
	height: 8px;
	background-color: #070716; }




.sm-visible {
	display: none; }
.xs-visible {
	display: none; }
.md-visible {
	display: none; }
